<template><section>
<h2 id="select-xuan-ze-qi"><a class="header-anchor" href="#select-xuan-ze-qi" aria-hidden="true">¶</a> Select 选择器</h2>
<p>当选项过多时，使用下拉菜单展示并选择内容。</p>
<h3 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h3>
<p>适用广泛的基础单选</p>
<docs-demo-block> 
                                                <div class="source" slot="source"><fm-select v-model="val">
    <fm-option v-for="(item, index) in options2" :key="index" :value="item.value" :label="item.label"></fm-option>
</fm-select>
<fm-select v-model="val2">
    <fm-option value="" label="全部"></fm-option>
    <fm-option v-for="(item, index) in options" :key="index" :value="item" :label="item"></fm-option>
</fm-select>


</div>
                                                <p><code>v-model</code> 的值为当前被选中的 <code>fm-option</code> 的 <code>value</code> 属性值</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">fm-select</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-option</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"(item, index) in options2"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"index"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"item.value"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"item.label"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-option</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">fm-select</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">fm-select</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val2"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-option</span> <span class="hljs-attr">value</span>=<span class="hljs-string">""</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"全部"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-option</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-option</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"(item, index) in options"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"index"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"item"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"item"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-option</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">fm-select</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">val</span>: <span class="hljs-literal">true</span>,
                <span class="hljs-attr">val2</span>: <span class="hljs-string">'选择2'</span>,
                <span class="hljs-attr">options</span>: [<span class="hljs-string">'选择1'</span>, <span class="hljs-string">'选择2'</span>, <span class="hljs-string">'选择3'</span>],
                <span class="hljs-attr">options2</span>: [
                    { <span class="hljs-attr">value</span>: <span class="hljs-literal">true</span>, <span class="hljs-attr">label</span>: <span class="hljs-string">'展示给跟随者'</span> },
                    { <span class="hljs-attr">value</span>: <span class="hljs-literal">false</span>, <span class="hljs-attr">label</span>: <span class="hljs-string">'不展示给跟随者'</span> }
                ]
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="jin-yong-zhuang-tai"><a class="header-anchor" href="#jin-yong-zhuang-tai" aria-hidden="true">¶</a> 禁用状态</h3>
<docs-demo-block> 
                                                <div class="source" slot="source">    <fm-select disabled>
        <fm-option v-for="(item, index) in options" :key="index" :value="item" :label="item"></fm-option>
    </fm-select>   
</div>
                                                <p>通过 <code>disabled</code> 属性指定是否禁用 select 组件</p>
<pre v-pre=""><code class="hljs language-html">    <span class="hljs-tag">&lt;<span class="hljs-name">fm-select</span> <span class="hljs-attr">disabled</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">fm-option</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"(item, index) in options"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"index"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"item"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"item"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-option</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">fm-select</span>&gt;</span>   
</code></pre>
</docs-demo-block>
<h3 id="select-shu-xing"><a class="header-anchor" href="#select-shu-xing" aria-hidden="true">¶</a> Select 属性</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">value</td>
<td style="text-align:left">绑定值</td>
<td style="text-align:left">string/number/boolean</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
</tr>
<tr>
<td style="text-align:left">disabled</td>
<td style="text-align:left">是否禁用</td>
<td style="text-align:left">boolean</td>
<td style="text-align:left">—</td>
<td style="text-align:left">false</td>
</tr>
<tr>
<td style="text-align:left">placeholder</td>
<td style="text-align:left">输入框占位文本</td>
<td style="text-align:left">string</td>
<td style="text-align:left">—</td>
<td style="text-align:left">请选择/Please select</td>
</tr>
</tbody>
</table>
<h3 id="option-shu-xing"><a class="header-anchor" href="#option-shu-xing" aria-hidden="true">¶</a> Option 属性</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">value</td>
<td style="text-align:left">选项的值, 若该值为假则与 <code v-pre="">label</code> 相同</td>
<td style="text-align:left">string/number/boolean</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
</tr>
<tr>
<td style="text-align:left">label</td>
<td style="text-align:left">选项的标签，若不设置则默认与 <code v-pre="">value</code> 相同</td>
<td style="text-align:left">string/number</td>
<td style="text-align:left">—</td>
<td style="text-align:left">-</td>
</tr>
</tbody>
</table>
<h3 id="shi-jian"><a class="header-anchor" href="#shi-jian" aria-hidden="true">¶</a> 事件</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">事件名称</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">change</td>
<td style="text-align:left">选值改变时触发</td>
<td style="text-align:left">(value: string | number | boolean)</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
        data () {
            return {
                val: true,
                val2: '选择2',
                options: ['选择1', '选择2', '选择3'],
                options2: [
                    { value: true, label: '展示给跟随者' },
                    { value: false, label: '不展示给跟随者' }
                ]
            }
        }
    }
</script>


